<script>import { BaseButton } from "@gradio/button";
export let code;
let copy_text = "copy";
function copy() {
  navigator.clipboard.writeText(code);
  copy_text = "copied!";
  setTimeout(() => {
    copy_text = "copy";
  }, 1500);
}
</script>

<BaseButton size="sm" on:click={copy}>
	{copy_text}
</BaseButton>
