<script>import { onMount, createEventDispatcher } from "svelte";
import Render from "./Render.svelte";
export let rootNode;
export let root;
export let target;
export let theme_mode;
export let version;
export let autoscroll;
export let max_file_size = null;
export let client;
const dispatch = createEventDispatcher();
onMount(() => {
  dispatch("mount");
});
</script>

{#if rootNode}
	<Render
		node={rootNode}
		{root}
		{target}
		{theme_mode}
		{version}
		{autoscroll}
		{max_file_size}
		{client}
	/>
{/if}
